.ant-form {
    color: var(--color-black);
    font-feature-settings: 'tnum', 'tnum';
    font-variant: tabular-nums;
    line-height: 1.5715;
    list-style: none;
}

.ant-form-item-explain-error {
    color: var(--color-red) !important;
}

.ant-select-status-error.ant-select:not(.ant-select-disabled):not(.ant-select-customize-input):not(.ant-pagination-size-changer)
    .ant-select-selector {
    border-color: var(--color-white-red) !important;
}

.ant-form-item-explain,
.ant-form-item-extra {
    clear: both;
    color: var(--color-black);
    line-height: 1.5715;
    min-height: 24px;
    padding-top: 2px;
    transition: color 0.3s cubic-bezier(0.215, 0.61, 0.355, 1);
    -webkit-transition: color 0.3s cubic-bezier(0.215, 0.61, 0.355, 1);
    -moz-transition: color 0.3s cubic-bezier(0.215, 0.61, 0.355, 1);
    -ms-transition: color 0.3s cubic-bezier(0.215, 0.61, 0.355, 1);
    -o-transition: color 0.3s cubic-bezier(0.215, 0.61, 0.355, 1);
}

.ant-form-item {
    flex-direction: column;
    margin-bottom: 16px;
    text-align: left;
}

.ant-form-item-control-input {
    align-items: center;
    display: flex;
    position: relative;
}

.ant-form-item .ant-form-item-explain,
.ant-form-item .ant-form-item-extra {
    font-size: 10.5px;
    letter-spacing: 0.5px;
    margin-bottom: 10px;
    min-height: auto !important;
}

.ant-form-item input::placeholder,
.ant-form-item .ant-select-selection-placeholder {
    line-height: 30px;
}

.ant-form-item .ant-form-item-label {
    display: block;
    height: 1em;
    line-height: 1em;
    margin: 0 0 5px 2px;
    overflow: visible;
    text-align: left;
}

.ant-form-item .ant-form-item-label label {
    color: var(--color-black);
    font-size: 9px;
    font-weight: 700;
    height: auto;
    letter-spacing: 0.2px;
    text-transform: uppercase;
}

.ant-form-item .ant-form-item-label label::after {
    content: none;
    margin: 0;
}

.ant-form-item
    .ant-form-item-label
    label.ant-form-item-required:not(.ant-form-item-required-mark-optional)::before {
    content: none;
}

.ant-form-item
    .ant-form-item-label
    label.ant-form-item-required:not(.ant-form-item-required-mark-optional)::after {
    color: var(--color-black-50);
    content: '*';
    display: inline-block;
    font-size: 14px;
    margin: 0 0 0 2px;
    position: relative;
    top: 1px;
}

.ant-form-item .ant-form-item-label label .ant-form-item-tooltip {
    color: var(--color-yellow);
    font-size: 14px;
    margin: 0;
    padding: 0;
    position: absolute;
    right: -18px;
    top: -2px;
}

.ant-form-item-has-error
    .ant-select:not(.ant-select-disabled):not(.ant-select-customize-input)
    .ant-select-selector {
    background-color: var(--color-white);
    border-color: var(--color-white-red) !important;
}

.ant-form-item-has-error .ant-input-number:not([disabled]):hover,
.ant-form-item-has-error .ant-picker:not([disabled]):hover {
    border-color: var(--color-white-red) !important;
}

.ant-form-item-has-error .ant-input-number,
.ant-form-item-has-error .ant-picker {
    border-color: var(--color-white-red) !important;
}

.ant-form-item-has-error
    :not(.ant-input-affix-wrapper-disabled):not(.ant-input-affix-wrapper-borderless).ant-input-affix-wrapper,
.ant-form-item-has-error
    :not(.ant-input-affix-wrapper-disabled):not(.ant-input-affix-wrapper-borderless).ant-input-affix-wrapper:hover,
.ant-form-item-has-error
    :not(.ant-input-disabled):not(.ant-input-borderless).ant-input,
.ant-form-item-has-error
    :not(.ant-input-disabled):not(.ant-input-borderless).ant-input:hover {
    border-color: var(--color-white-red);
}

.input-table .ant-form-item {
    float: right;
    margin: -10px 0;
    padding: 0;
}

.input-table .ant-form-item .ant-input-group .ant-input {
    font-size: 11px;
    padding: 6px 10px;
}

.ant-table .input-table a {
    float: right;
    margin: 2px 16px -13px 16px;
    padding: 0;
}

.ant-input-group-addon {
    color: var(--color-black);
    border-radius: 6px;
    -webkit-border-radius: 6px;
    -moz-border-radius: 6px;
    -ms-border-radius: 6px;
    -o-border-radius: 6px;
}

.ant-cascader-menu-item {
    align-items: center;
    color: var(--color-black);
    cursor: pointer;
    display: flex;
    flex-wrap: nowrap;
    line-height: 18px;
    overflow: hidden;
    padding: 5px 8px;
    text-overflow: ellipsis;
    transition: all 0.3s;
    white-space: nowrap;
}

.ant-picker-cell-in-view.ant-picker-cell-in-range::before {
    background: var(--color-green);
}

.ant-picker-cell-in-view.ant-picker-cell-range-start:not(.ant-picker-cell-range-start-single)::before,
.ant-picker-cell-in-view.ant-picker-cell-range-end:not(.ant-picker-cell-range-end-single)::before {
    background: var(--color-green);
}
