/*  <Button> */

.mos-btn.ant-btn {
    background: none;
    border-color: var(--color-grey-purple);
    color: var(--color-purple);
    font-size: 9px;
    height: 32px;
    padding: 0 22px;
    text-shadow: none !important;
    text-transform: uppercase;
    border-radius: 28px;
    -webkit-border-radius: 28px;
    -moz-border-radius: 28px;
    -ms-border-radius: 28px;
    -o-border-radius: 28px;
}

.mos-btn.ant-btn:hover,
.mos-btn.ant-btn:focus,
.mos-btn.ant-btn:active {
    background: none;
    border-color: var(--color-purple);
    color: var(--color-purple);
}

.mos-btn.ant-btn span {
    color: inherit;
    font-family: inherit;
    font-size: inherit;
    font-weight: 600;
    letter-spacing: 1px;
    line-height: 20px;
}

.mos-btn.ant-btn span.anticon {
    display: block;
    font-size: 16px !important;
    left: 12px;
    margin: 0;
    position: absolute;
    top: 7px;
}

.mos-btn.ant-btn > .anticon + span,
.mos-btn.ant-btn > span + .anticon {
    padding-left: 12px;
}

.mos-btn.ant-btn span.anticon.right {
    display: block;
    left: auto;
    margin: 0;
    position: absolute;
    right: 12px;
}

.mos-btn.ant-btn > .anticon.right + span,
.mos-btn.ant-btn > span + .anticon.right {
    padding-left: 0;
    padding-right: 12px;
}

.mos-btn.ant-btn span.anticon.maticon {
    top: 4.5px;
}

.mos-btn.ant-btn.ant-btn-lg {
    font-size: 10px;
    padding: 0 32px;
}

.mos-btn.ant-btn.ant-btn-primary,
.mos-btn.ant-btn.ant-btn-primary:focus,
.mos-btn.ant-btn.ant-btn-primary:active {
    background: var(--color-purple);
    border-color: var(--color-purple);
    color: var(--color-white);
}

.mos-btn.ant-btn.ant-btn-primary:hover {
    background: var(--color-dark-purple);
}

.mos-btn.ant-btn.ant-btn-dashed,
.mos-btn.ant-btn.ant-btn-dashed:focus,
.mos-btn.ant-btn.ant-btn-dashed:active {
    background: var(--color-green);
    border-color: var(--color-green);
    color: var(--color-white);
}

.mos-btn.ant-btn.ant-btn-dashed:hover {
    background: var(--color-dark-green);
    border-color: var(--color-dark-green);
}

.mos-btn.ant-btn.ant-btn-icon-only {
    height: 36px;
    line-height: 36px;
    margin: 0;
    padding: 0;
    width: 36px;
}

.mos-btn.ant-btn.ant-btn-icon-only span.anticon {
    left: 9px;
    top: 9px;
}

.mos-btn.ant-btn.ant-btn-icon-only span.anticon.maticon {
    top: 6.5px;
}

.mos-btn.ant-btn-round.ant-btn-lg {
    margin-top: 2px;
}
