/* fonts */

@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,600;1,700;1,800;1,900&display=swap');
/* colors */

:root {
    --color-black: #777777;
    --color-black-label: #707070;
    --color-black-label-light: #8c8c8c;
    --color-black-100: #000000;
    --color-black-75: #8a8a8a;
    --color-black-50: #999999;
    --color-black-25: #bbbbbb;
    --color-black-10: #e0e0e0;
    --color-black-5: #eaeaea;
    --color-black-1: #fafafa;

    --color-white: #ffffff;
    --color-white-50: #ffffff88;
    --color-white-25: #ffffff44;

    --color-blue: #427fa3;
    --color-green: #54bbab;
    --color-purple: #563062;
    --color-red: #d16476;
    --color-yellow: #ecc505;

    --color-dark-blue: #55637e;
    --color-dark-green: #4f998d;
    --color-dark-purple: #2e193d;
    --color-dark-red: #7c3c5b;
    --color-dark-yellow: #d2930f;

    --color-light-blue: #aacde2;
    --color-light-green: #c0e5df;
    --color-light-purple: #e4dff8;
    --color-light-red: #f8e6e6;
    --color-light-yellow: #f5eab3;

    --color-opaque-green: #ecfaf5;
    --color-opaque-blue: #eceff8;
    --color-opaque-red: #ffe9e9;
    --color-opaque-yellow: #fffbe4;

    --color-medium-purple: #9078a0;
    --color-blue-0: #f7f7fd;
    --color-light-grey: #ccc;
    --color-grey-purple: #b8aebb;
    --color-white-red: #cc9090;
    --color-dark-grey-purple: #1f1a33;

    --font-family-sans-serif: -apple-system, BlinkMacSystemFont, 'Segoe UI',
        Roboto, Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif,
        'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';

    --font-family-monospace: 'Menlo', 'DejaVu Sans Mono', 'Liberation Mono',
        'Consolas', 'Ubuntu Mono', 'Courier New', 'andale mono',
        'lucida console', monospace;
}

/* focus */

*:focus {
    outline: none !important;
}

/* selection */

::-moz-selection {
    color: var(--color-white);
    background: var(--color-green);
}

::selection {
    color: var(--color-white);
    background: var(--color-green);
}

/* scroll */

::-webkit-scrollbar {
    height: 8px !important;
    width: 8px;
}

::-webkit-scrollbar-track {
    background: var(--color-black-10);
}

::-webkit-scrollbar-thumb {
    background-clip: padding-box;
    background: var(--color-black-50);
}

::-webkit-scrollbar-thumb:hover {
    background: var(--color-black);
}

/* helpers */

.nowrap {
    white-space: nowrap !important;
}

.wrap {
    white-space: normal !important;
}

.middle {
    display: flex;
    align-items: center;
}

/* html */

* {
    font-family: 'Montserrat', sans-serif !important;
    letter-spacing: 0.4px;
}

a {
    color: var(--color-purple);
}

a:hover {
    color: var(--color-dark-purple);
    text-decoration: underline;
}

/* antd fix */

.ant-input-affix-wrapper {
    background-color: inherit !important;
}
