.ant-section {
    background: var(--color-white);
    border: 1px solid #c0e5df;
    padding: 44px;
    position: relative;
    min-height: 160px;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
}

.ant-section.small {
    padding: 22px;
    min-height: auto;
}

.ant-section.input {
    margin-bottom: 16px;
    min-height: auto;
    padding: 22px 22px 22px 22px;
}
