.qrcode-active {
    border-radius: 10px;
    border: 4px solid #3f9d8f;
    display: block;
    margin: 20px auto 30px auto;
    max-width: 720px;
    overflow: hidden;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
}
