/* -------------------------------------------------------------------------- */

.ant-tooltip {
    color: var(--color-white);
}

.ant-tooltip .ant-tooltip-inner {
    background: var(--color-purple);
    font-size: 12px;
    font-weight: 500;
    letter-spacing: 0.2px;
    padding: 8px 12px;
    border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
    -webkit-border-radius: 5px;
}

.ant-tooltip .ant-tooltip-arrow-content:before {
    background: var(--color-purple);
    height: 25px;
    width: 25px;
}

.ant-tooltip .ant-tooltip-inner small {
    font-size: 9px;
    opacity: 0.7;
    display: block;
    font-weight: normal;
}

.ant-tooltip .ant-tooltip-inner span {
    font-size: 10px;
    font-weight: normal;
}

/* -------------------------------------------------------------------------- */

.ant-tooltip.ant-menu-inline-collapsed-tooltip .ant-tooltip-inner {
    font-weight: bold;
    padding: 10px 20px;
}

/* -------------------------------------------------------------------------- */

.ant-tooltip.green .ant-tooltip-inner {
    background: var(--color-green);
}

.ant-tooltip.green .ant-tooltip-arrow-content:before {
    background: var(--color-green) !important;
}

.ant-tooltip.red .ant-tooltip-inner {
    background: var(--color-red);
}

.ant-tooltip.red .ant-tooltip-arrow-content:before {
    background: var(--color-red) !important;
}

/* -------------------------------------------------------------------------- */

.ant-tooltip.form {
    max-width: 400px;
    padding-bottom: 7px;
}

.ant-tooltip.form .ant-tooltip-inner {
    background: var(--color-white);
    border-bottom: 3px solid var(--color-yellow);
    color: var(--color-black);
    padding: 12px 16px;
}

.ant-tooltip.form .ant-tooltip-arrow-content:before {
    background: var(--color-yellow);
}

.ant-tooltip.form.ant-tooltip-placement-topRight .ant-tooltip-content {
    margin-right: -17px;
}

.ant-tooltip.form.ant-tooltip-placement-topLeft .ant-tooltip-content {
    margin-left: -16px;
}

/* dark ===================================================================== */

body:has(.dark) .ant-tooltip {
    filter: invert(1) hue-rotate(170deg) !important;
}
