.ant-input {
    background-color: var(--color-white);
    border: 1px solid #c0e5df;
    box-sizing: border-box;
    color: var(--color-black);
    display: inline-block;
    height: 38px;
    line-height: 1.5715;
    list-style: none;
    margin: 0;
    min-width: 0;
    padding: 8.6px 11px 9.6px 11px;
    position: relative;
    transition: all 0.3s;
    width: 100%;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
}

.ant-input:hover {
    border-color: #54bbab;
    border-right-width: 1px !important;
}

.ant-input:focus,
.ant-input-focused {
    border-color: #54bbab;
    border-right-width: 1px !important;
    box-shadow: 0 0 0 2px rgba(84, 187, 171, 0.2);
    outline: 0;
}

.ant-input-affix-wrapper {
    border-radius: 5px;
    border: 1px solid c0e5df;
    height: 38px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
}

.ant-input-textarea-show-count::after {
    font-size: 11px !important;
    position: absolute;
    right: 1px;
    top: -19px;
}

.ant-input-affix-wrapper:focus,
.ant-input-affix-wrapper-focused {
    border-color: #54bbab;
    border-right-width: 1px !important;
    box-shadow: 0 0 0 2px rgba(84, 187, 171, 0.2);
    outline: 0;
}

.ant-input-affix-wrapper:focus,
.ant-input-affix-wrapper-focused {
    box-shadow: 0 0 0 2px rgba(84, 187, 171, 0.2);
    outline: 0;
}

.ant-input-affix-wrapper:not(.ant-input-affix-wrapper-disabled):hover {
    border-color: #54bbab;
    border-right-width: 1px !important;
    z-index: 1;
}

.ant-input-password-icon {
    color: rgba(0, 0, 0, 0.2);
}

.ant-input-modal {
    box-sizing: border-box;
    list-style: none;
    margin: 0;
    position: relative;
    width: 100%;
}

.ant-input-modal p {
    margin: 0;
}

.ant-input-modal .anticon-sync {
    color: var(--color-black-50);
    font-size: 13.5px;
    margin-right: 1px;
}

.ant-input-affix-wrapper > input.ant-input {
    height: 35px;
    margin-top: -3px;
}

.input-select__transfer .ant-modal-body {
    padding: 24px 70px;
}

.input-select__transfer .ant-table-wrapper ::-webkit-scrollbar {
    height: 10px !important;
    width: 5px;
}

.input-select__transfer .ant-table-wrapper ::-webkit-scrollbar-track {
    background: rgba(0, 0, 0, 0.02);
}

.input-select__transfer .ant-table-wrapper ::-webkit-scrollbar-thumb {
    background-clip: padding-box;
    background: rgba(0, 0, 0, 0.04);
}

.input-select__transfer .ant-table-wrapper ::-webkit-scrollbar-thumb:hover {
    background: rgba(0, 0, 0, 0.09);
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active,
input:-webkit-autofill::first-line {
    box-shadow: 0 0 0 30px var(--color-white) inset;
    color: var(--color-black-75) !important;
    font-family: 'Readex Pro' !important;
    transition: background-color 5000s ease-in-out 0s !important;
    -webkit-box-shadow: 0 0 0 30px var(--color-white) inset !important;
    -webkit-text-fill-color: none !important;
    -webkit-transition: background-color 5000s ease-in-out 0s !important;
    -moz-transition: background-color 5000s ease-in-out 0s !important;
    -ms-transition: background-color 5000s ease-in-out 0s !important;
    -o-transition: background-color 5000s ease-in-out 0s !important;
}

.ant-input-number {
    border: 1px solid c0e5df;
    color: var(--color-black);
    line-height: 1.5715;
    padding: 6px 11px;
    transition: all 0.3s;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
}

.ant-input-number:hover {
    border-color: #54bbab;
    border-right-width: 1px !important;
}

.ant-input-number:focus,
.ant-input-number-focused {
    box-shadow: 0 0 0 2px rgba(84, 187, 171, 0.2);
}

.ant-input-number-handler:hover .ant-input-number-handler-up-inner,
.ant-input-number-handler:hover .ant-input-number-handler-down-inner {
    color: #54bbab;
}

.ant-page-header-back-button:active,
.ant-page-header-back-button:focus,
.ant-page-header-back-button:hover {
    color: #54bbab !important;
}

.ant-input-prefix {
    font-size: 13px;
    margin: 0 8px 0 4px;
}

.ant-input-suffix small {
    color: var(--color-black-50);
    font-size: 9px;
    letter-spacing: 0.5px;
}
