.ant-select {
    color: var(--color-black);
    line-height: 1.5715;
    list-style: none;
    cursor: pointer;
}
.ant-select:not(.ant-select-customize-input) .ant-select-selector {
    position: relative;
    border: 1px solid var(--color-light-green);
    border-radius: 5px;
    transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
    height: 38px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
}
.ant-select:not(.ant-select-customize-input) .ant-select-selector:hover {
    border-color: var(--color-green) !important;
}
.ant-select-selector {
    height: 100%;
    display: flex;
    align-items: center;
}
.ant-select-single .ant-select-selector .ant-select-selection-search-input {
    width: 100%;
    height: 38px;
}
.ant-select-multiple .ant-select-selection-item {
    background: none !important;
    color: var(--color-black);
    border-radius: 5px;
    border: 1px solid var(--color-black-5);
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
}
.ant-select-multiple .ant-select-selector::after {
    display: inline-block;
    width: 0;
    margin: 5px 0;
    line-height: 20.8px;
    content: '\a0';
}
.ant-select-multiple .ant-select-selector {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    padding: 3.6px 9.6px;
}
.ant-select-selection-overflow {
    position: relative;
    display: flex;
    flex: auto;
    flex-wrap: wrap;
    max-width: 100%;
}
.ant-select-item,
.ant-select-item-empty {
    position: relative;
    display: block;
    min-height: 32px;
    padding: 5px 12px;
    font-weight: 400 !important;
    line-height: 22px;
}
.ant-select-item-option-selected {
    font-weight: 400;
    background: none !important;
    color: var(--color-black-5);
}
.ant-select-item-option-active {
    color: var(--color-green) !important;
}
.ant-select-dropdown,
.ant-picker-dropdown {
    z-index: 999999;
    color: var(--color-white);
    border-radius: 6px;
    -webkit-border-radius: 6px;
    -moz-border-radius: 6px;
    -ms-border-radius: 6px;
    -o-border-radius: 6px;
}
.ant-select-dropdown .rc-virtual-list-scrollbar-thumb,
.ant-picker-dropdown .rc-virtual-list-scrollbar-thumb {
    width: 50% !important;
    background: var(--color-black-10) !important;
}
.ant-select-disabled .ant-select-selector,
.ant-input-disabled,
.ant-picker-disabled,
.ant-input-affix-wrapper-disabled {
    background: var(--color-white) !important;
    border-color: var(--color-black-10) !important;
    opacity: 0.3 !important;
}
.ant-select-selection-search-input {
    margin-top: 5px !important;
}
.ant-select:not(.ant-select-disabled):hover .ant-select-selector {
    border-color: var(--color-green);
    border-right-width: 1px !important;
}
.ant-select-focused:not(.ant-select-disabled).ant-select:not(
        .ant-select-customize-input
    )
    .ant-select-selector {
    border-color: var(--color-green) !important;
    border-right-width: 1px !important;
    box-shadow: 0 0 0 0px rgba(84, 187, 171, 0.2);
}
.ant-select-multiple .ant-select-selection-placeholder {
    left: 14px;
}
.ant-select:not(.ant-select-disabled):hover .ant-select-selector {
    border-color: var(--color-green);
    border-right-width: 1px !important;
}
.ant-select:not(.ant-select-customize-input) .ant-select-selector {
    position: relative;
    background-color: var(--color-white);
    border: 1px solid var(--color-light-green);
    border-radius: 5px;
    transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
}
.ant-select-focused {
    border-color: var(--color-green);
    border-right-width: 3px !important;
    outline: 0;
    box-shadow: 0 0 0 0px rgba(84, 187, 171, 0.2);
}
.ant-select-focused:not(.ant-select-disabled).ant-select:not(
        .ant-select-customize-input
    )
    .ant-select-selector {
    border-color: var(--color-green);
    border-right-width: 1px !important;
    outline: 0;
    box-shadow: 0 0 0 2px rgba(84, 187, 171, 0.2);
}
.ant-select-item-option-selected:not(.ant-select-item-option-disabled)
    .ant-select-item-option-state {
    color: var(--color-green);
    display: block !important;
    position: inherit !important;
    padding-left: 94%;
    margin-top: -21px;
}
