.ant-picker {
    align-items: center;
    background: var(--color-white);
    border: 1px solid var(--color-light-green);
    box-sizing: border-box;
    color: var(--color-black);
    display: inline-flex;
    line-height: 1.5715;
    list-style: none;
    margin: 0;
    padding: 7px 11px;
    position: relative;
    width: 100% !important;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
    transition: border 0.3s, box-shadow 0.3s;
    -webkit-transition: border 0.3s, box-shadow 0.3s;
    -moz-transition: border 0.3s, box-shadow 0.3s;
    -ms-transition: border 0.3s, box-shadow 0.3s;
    -o-transition: border 0.3s, box-shadow 0.3s;
}

.ant-picker:hover {
    border-color: var(--color-green);
    border-right-width: 1px;
}

.ant-picker-focused {
    border-color: var(--color-green);
    border-right-width: 1px;
    box-shadow: 0 0 0 2px rgba(84, 187, 171, 0.2);
    outline: 0;
}

.ant-picker-today-btn,
.ant-picker-today-btn:hover {
    color: var(--color-green);
}

.ant-picker-panel-container {
    box-shadow: 8px 8px 8px -2px rgba(0, 0, 0, 0.1),
        -8px 2px 8px -2px rgba(0, 0, 0, 0.05);
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
}

.ant-picker-panel-container .ant-picker-cell-disabled .ant-picker-cell-inner {
    color: #eee;
}

.ant-picker-panel-container .ant-picker-cell-inner:hover {
    background: var(--color-light-green);
}

.ant-picker-cell-in-view.ant-picker-cell-today .ant-picker-cell-inner::before {
    border: 1px solid var(--color-green);
}

.ant-picker-cell-in-view.ant-picker-cell-selected .ant-picker-cell-inner {
    background: var(--color-green);
    color: var(--color-white);
}

.ant-picker-input > input {
    background-color: var(--color-white);
    background: 0 0;
    border: 0;
    color: var(--color-black);
    display: inline-block;
    flex: auto;
    height: auto;
    line-height: 1.5715;
    min-width: 0;
    min-width: 1px;
    padding: 0;
    position: relative;
    transition: all 0.3s;
    width: 100%;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
}

.ant-picker-range .ant-picker-active-bar {
    background: var(--color-purple);
}

.ant-picker-header-view button:hover {
    color: var(--color-green);
}

.ant-picker-cell-in-view.ant-picker-cell-selected .ant-picker-cell-inner,
.ant-picker-cell-in-view.ant-picker-cell-range-start .ant-picker-cell-inner,
.ant-picker-cell-in-view.ant-picker-cell-range-end .ant-picker-cell-inner {
    background: var(--color-light-green);
}

.ant-picker-range .ant-picker-clear {
    right: 11px;
}
.ant-picker-cell-in-view.ant-picker-cell-range-hover-start:not(
        .ant-picker-cell-in-range
    ):not(.ant-picker-cell-range-start):not(.ant-picker-cell-range-end)::after,
.ant-picker-cell-in-view.ant-picker-cell-range-hover-end:not(
        .ant-picker-cell-in-range
    ):not(.ant-picker-cell-range-start):not(.ant-picker-cell-range-end)::after,
.ant-picker-cell-in-view.ant-picker-cell-range-hover-start.ant-picker-cell-range-start-single::after,
.ant-picker-cell-in-view.ant-picker-cell-range-hover-start.ant-picker-cell-range-start.ant-picker-cell-range-end.ant-picker-cell-range-end-near-hover::after,
.ant-picker-cell-in-view.ant-picker-cell-range-hover-end.ant-picker-cell-range-start.ant-picker-cell-range-end.ant-picker-cell-range-start-near-hover::after,
.ant-picker-cell-in-view.ant-picker-cell-range-hover-end.ant-picker-cell-range-end-single::after,
.ant-picker-cell-in-view.ant-picker-cell-range-hover:not(
        .ant-picker-cell-in-range
    )::after {
    border-top: 1px dashed var(--color-green);
    border-bottom: 1px dashed var(--color-green);
    border-color: var(--color-green) !important;
}
